.PopupComponent {
    position: fixed;
    width: 400px;
    height: 500px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    border-radius: 10px;
    background: #fff;
    overflow: hidden;
    z-index: 9999;
    box-shadow: inset 0 0 1px 0 #000;

    .modal{
        background-color: #fff !important;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 400px;
        height: 500px;
        margin: auto;
        background: #000;
        opacity: .6;
        z-index: 9998;
        border-radius: 10px;
        padding: 40px 30px 0 30px;
        .modal_title{
            font-size: 30px;
            color: black;
        }
        .modal_content{
            margin-top: 20px;
        }
        .modal_icon{
            position: absolute;
            top: 550px;
            left: 170px;
            // background-color: blue;
        }
    }
    .mask {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #000;
        opacity: .6;
        z-index: 9991;
    }
  }

  
